.block-ui {
  position: relative;
}

body.block-ui {
  /* IE8 doesn't support .block-ui:not(body) */
  
  position: static;
}

body.block-ui > .block-ui-container,
.block-ui-main > .block-ui-container {
  position: fixed;
}

.block-ui-container {
  position: absolute;
  z-index: 10000;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 0;
  overflow: hidden;
  opacity: 0;
  filter: alpha(opacity=00);
}

.block-ui-active > .block-ui-container {
  height: 100%;
  cursor: wait;
}

.block-ui-active .block-ui-active > .block-ui-container {
  height: 0;
}

.block-ui-visible > .block-ui-container {
  opacity: 1;
  filter: alpha(opacity=100);
}

.block-ui-overlay {
  width: 100%;
  height: 100%;
  opacity: 0.5;
  filter: alpha(opacity=50);
  background-color: white;
}

.block-ui-message-container {
  position: absolute;
  top: 35%;
  left: 0;
  right: 0;
  height: 0;
  text-align: center;
  z-index: 10001;
}

.block-ui-message {
  display: inline-block;
  text-align: left;
  background-color: #8f8f8f;
  color: #f5f5f5;
  padding: 20px;
  border-radius: 4px;
  font-size: 20px;
  font-weight: bold;
  /* needed for IE */
  
  filter: alpha(opacity=100);
}
/*custom loading*/
@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}
.loadIcon {
  margin: 60px auto;
  height: 120px;
  width: 120px;
  position: relative;
}
.loadIcon div {
  position: absolute;
}
.loadIcon div:first-child {
  height: 120px;
  width: 120px;
  border-radius: 60px;
  border-top: solid 10px #174158;
  animation: spin 2s infinite;
  -webkit-animation: spin 2s infinite;
  animation-timing-function: linear;
  -webkit-animation-timing-function: linear;
}
.loadIcon div:nth-child(2) {
  height: 90px;
  width: 90px;
  border-radius: 45px;
  border-top: solid 10px #41748d;
  top: 15px;
  left: 15px;
  animation: spin 1.5s infinite;
  -webkit-animation: spin 1.5s infinite;
  animation-timing-function: linear;
  -webkit-animation-timing-function: linear;
}
.loadIcon div:nth-child(3) {
  height: 60px;
  width: 60px;
  border-radius: 30px;
  border-top: solid 10px #ff1a01;
  top: 30px;
  left: 30px;
  animation: spin 1s infinite;
  -webkit-animation: spin 1s infinite;
  animation-timing-function: linear;
  -webkit-animation-timing-function: linear;
}
.loadIcon div:nth-child(4) {
  height: 30px;
  width: 30px;
  border-radius: 15px;
  border-top: solid 10px #c61901;
  top: 45px;
  left: 45px;
  animation: spin 0.5s infinite;
  -webkit-animation: spin 0.5s infinite;
  animation-timing-function: linear;
  -webkit-animation-timing-function: linear;
}